.app {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin-bottom: 25vh;
}

.app > .title {
	text-align: center;
	margin-top: 100px;
	font-size: 32px;
}

.app > * {
	flex-basis: 100%;
}

@media screen and (max-width: 400px) {
	.app > .title {
		font-size: 24px;
	}
}

.app .front-image-wrapper {
	max-width: 70vw;
	overflow: hidden;
}

.app .front-image-wrapper .front-image {
	background-image: url("../front-image.jpg");
	height: 80vh;
	margin-top: 10vh;
	margin-bottom: 100px;
    background-position: center;
	background-repeat: no-repeat;
	background-size: 50%;
}

@media screen and (max-width: 400px) {
	.app .front-image-wrapper .front-image {
		height: 60vh;
		margin-top: 20vh;
		margin-bottom: 50px;
	}
}