.time-travel-button {
	border-radius: 70px;
	padding: 8.5px;
    overflow: hidden;
}

.time-travel-button img {
	cursor: pointer;
	display: block;
	margin: 0;
	padding: 0;
	width: 20vw;
	max-width: 100px;
	transform: scale(1);
	filter: grayscale(0);
	transition: filter 0.3s ease-in-out, transform 0.1s ease-in-out;
}

.time-travel-button img:active {
	transform: scale(0.95);
}

.time-travel-button.disabled img, .time-travel-button[disabled] img {
	filter: grayscale(0.7);
}