.story-line {
	box-sizing: border-box;
	transition: height 1s ease-in-out;
}

.story-line .story-wrapper {
}

.story-line .story-wrapper > p {
	font-size: 16px;
	font-family: Verdana;
}

.story-line .story-wrapper > p:nth-child(1) {
	margin-top: 0px;
}

.story-line .story-wrapper > p:nth-last-child(1), .story-line .story-wrapper > p:nth-last-child(1) > *:nth-last-child(1) {
	margin-bottom: 0px;
}

.story-line .story-action button {
	display: block;
	margin-bottom: 10px;
}

.gradient-hider {
	position: relative;
	background: transparent;
	background: linear-gradient(0deg, white 0%, transparent 70%, transparent 100%);
}

.gradient-hider.with-transition {
	transition: height 1.5s ease-in-out;
}

.gradient-hider.with-fast-transition {
	transition: height 0.5s ease-in-out;
}